.send-email-form {
    max-width: 700px;
    margin: auto;
}

.send-email-form h3 {
    font-weight: 500;
}
.send-email-form .form__form-group-label{
    font-weight: 700;
}
.send-email-form select, .send-email-form input {
    width: 100%;
    padding: 13px 10px;
    border-radius: 4px;
    border: 1px solid #eee;
}

