// old css
.client-reg-form {
	max-width: 700px;
	margin: auto;
	.form__form-group-label {
		font-weight: 700;
	}
}
.form__form-group-client-fields {
	display: flex;
	width: 100%;
	>div {
		&:not(:last-child) {
			margin-right: 20px;
		}
		input {
			width: 100%;
		}
		flex: 1;
	}
}
.client-search-container {
	.card-body {
		.row {
			max-width: 700px;
			margin: auto;
		}
	}
	form {
		input {
			width: 100%;
			padding: 8px;
			border: 1px solid rgb(118, 118, 118);
			width: 100%;
			border: 1px solid #E9ECEF;
			padding: 8px 15px;
		}
		select {
			width: 100%;
			padding: 8px;
			border: 1px solid rgb(118, 118, 118);
			width: 100%;
			padding: 13px 8px;
			border-radius: 2px;
			border: 1px solid #E9ECEF;
		}
	}
}
.form__form-group-two {
	>* {
		flex: 0 0 48%;
	}
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.client-details-left {
	>* {
		&:not(:first-child) {
			&:not(:last-child) {
				margin: 4px 0px 15px;
			}
		}
	}

	h3{
		color: #383938;
		font-family: 'Raleway', sans-serif;
		font-size: 24px;
	}
	> span {
		display: inline-block;
	}
	.outstanding-icon {
		margin-right: 10px !important;
		transform: translateY(-2px);
	}
	.toggle-switch{
		margin-top: -4px;
	}
	// display: flex;
	// align-items: center;
	
}
.is-active {
	background-color: #0BCE97;
	border-radius: 4px;
	padding: 2px 6px;
	color: white;
}

.not-active {
	border-radius: 4px;
	padding: 2px 6px;
	color: #313a46;
	background-color: #e3eaef;
}
.no-balance,
.with-balance{
	font-size: 16px;
	font-weight: 500;
	font-family: 'Raleway', sans-serif;
}
.no-balance {
	color: #00C48C;
}
.with-balance {
	color: #fa5c7c;	
}

.client-details-body {
	padding: 33px 30px;
	margin: 1.25rem 0;
	background: rgba(231, 242, 250, 0.5) !important;
	box-shadow: 0px 3px 3px 0px #ececf1 !important;
	// height: auto;
	flex: unset;
}
.card-header {
	background: transparent !important;
	border: none;
	padding: 0;
	padding-bottom: 20px;
	// background-color: $theme-yellow;
	p {
		color: #383938;
		font-weight: 700;
		font-size: 14px;
		font-family: 'Raleway', sans-serif;
	}
}
.card-content {
	p {
		font-family: 'Raleway', sans-serif;
	}
	.card-detail-label {
		color: #787878;
		font-weight: 400;
		font-size: 12px;

		span{
			color: #383938;
			font-weight: 500;
		}
	}
	p:not(.card-detail-label) {
		margin-bottom: 20px;
		margin-top: 5px;
		color: #383938;
		font-weight: 500;
	}
	p:last-child{
		margin-bottom: 0;
	}

	.btn{
		background: white !important;
	}
}

.cardbody-controlled {
	height: auto !important;
	flex: unset;
	.nav {
		border-bottom: 2px solid rgba(152, 166, 173, 0.2);
		padding: 0;
		a {
			border: none;
			border-bottom: 2px solid transparent;
			color: #6c757d;
			font-weight: 600;
			&:hover {
				border-bottom: 2px solid $theme-yellow;
			}
		}
		a.active {
			border-bottom: 2px solid $theme-yellow;
		}
	}
}
.card-controlled {
	height: auto !important;
}
.client-activities-list {
	display: flex;
	padding: 20px 0 30px;
	&:not(:last-child) {
		border-bottom: 1px solid #eee;
	}
}
.client-avatar-wrapper {
	img {
		width: 50px;
		height: 50px;
		object-fit: cover;
	}
}
.client-activity-details {
	padding-left: 20px;
	flex: 1;
}
.client-meta {
	h3 {
		font-weight: 700;
		small {
			font-size: 14px;
		}
	}
}
.client-activity-content {
	padding: 15px 20px;
	box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.2);
	border-radius: 4px;
	margin-left: 30px;
}
.property-search-btns {
	text-align: right;
}
.client-registration-form-container {
	form {
		select {
			width: 100%;
			padding: 13px 8px;
			border-radius: 2px;
			border: 1px solid #E9ECEF;
			outline: 0;

			&:focus{
				border-color: #009adc;
			}
		}
		input {
			width: 100%;
			border: 1px solid #E9ECEF;
			padding: 8px 15px;
			outline: 0;

			&:focus{
				border-color: #009adc;
			}
		}
		input[type="email"] {
			+ {
				button {
					width: auto;
					margin: 0;
					max-width: none !important;
				}
			}
			width: auto;
			flex: 1;
			height: auto;
			margin-right: 10px;
		}
    }
    textarea {
        width: 100%;
        border: 1px solid #E9ECEF;
        padding: 8px 15px;
		outline: 0;

		&:focus{
			border-color: #009adc;
		}
    }
}
.client-create-btns, .send-email-btns, .edit-county-btns, .property-search-btns {
	button {
		flex: unset;
		width: auto;
		&:after {
			display: none;
		}
		&:before {
			display: none;
		}
		svg {
			margin-bottom: 3px;
		}
	}
	display: flex;
	// justify-content: flex-end;
	.btn-primary {
		background: $theme-yellow !important;
		border-color: $theme-yellow !important;
		border-radius: 5px;
		color: #fff;
		transition: all 0.3s ease-in-out;
		-webkit-box-shadow: 0px 2px 6px 0px $yellow;
		box-shadow: 0px 2px 6px 0px $yellow;
		svg:not(.custom) {
			path {
				fill: #fff;
			}
		}
	}
}

.btn-secondary{
	border-color: $theme-yellow !important;
	background: transparent !important;
	color: #383938;
	border-radius: 5px;
	font-weight: 500;
	font-family: 'Raleway', sans-serif;
	// padding: 5px 25px;
	&:before {
		display: none;
	}

	svg{
		margin-top: 0;
	}
}
.btn-primary {
	background: $theme-yellow !important;
	border-color: $theme-yellow !important;
	border-radius: 5px;
	color: #fff !important;
	transition: all 0.3s ease-in-out;
	-webkit-box-shadow: none;
	box-shadow: none;
	svg:not(.custom) {
		path {
			fill: #fff;
		}
	}
	&:before {
		display: none;
	}
	&:after {
		display: none;
	}

	svg.custom{
		fill: none !important;
	}
}

.btn-primary.primary-2{
	padding: 5px 25px;
}

.btn-tertiary{
	border: none;
    background: none !important;
    padding: 0px;
    color: #383938 !important;
    margin-bottom: 0;

	&:before {
		display: none;
	}
}

.btn-cancel{
	background: rgba(56, 57, 56, 0.35) !important;
	color: white !important; 

	&:before, &:after{
		display: none;
	}
}

.btn-secondary.secondary-2{
	padding: 10px 25px;
}

.btn-secondary.secondary-inverted{
	border-color: white !important;
	color: white !important;
}
