.property-container, .property-container h2, .property-container h3 {
    color: white;
}
.property-container h2 {
    margin-bottom: 30px;
    text-decoration: underline;
}

.percent-sav h3 {
    background: white;
    padding: 5px 10px;
    border-radius: 4px;
    color: #002D62;
    font-weight: 700;
    margin-bottom: 20px;
    font-size: 20px;
}

.percent-sav h3 span {
    font-weight: 400;
}
.edit-property-form{
    width: 100%;
}
.property-search-container .card-body .row,
#add_billing_form {
    max-width: 700px;
    margin: auto;
}

.property-search-container form input,
.property-search-container form select {
    width: 100%;
    padding: 8px;
    border: 1px solid rgb(118, 118, 118);
}
.form__form-group-two > * {
    flex: 0 0 48%;
}
.form__form-group-two {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.property-search-btns button svg,
.property-create-btns button svg{
    margin-bottom: 3px;
}
.property-create-btns .create__btn svg path {
    fill: #fff;
}
