.general-results-files > div {
    box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.1);
} */

.general-files-list {
    border: 1px solid #FFA647;
    width: 100%;
    padding: 20px 15px;
    border-radius: 2px;
    margin-top: 20px !important;
    border-radius: 5px;
}
.general-files-list > div {
    display: flex;
    align-items: center;
}
.docu-delete-wrapper {
    justify-content: space-between;
}
.docu-delete-wrapper > div:last-child {
    flex: 0 0 30%;
}


.general-files-list  .filetype {
    background-color: #009ADC;
    color: white;
    font-weight: 600;
    line-height: 45px;
    width: 45px;
    border-radius: 4px;
    display: inline-block;
    text-align: center;
    margin-right: 15px;
    font-size: 11px;
    font-family: 'Raleway', sans-serif;
}

.general-files-list  .filename {
    flex: 1 1;
    color: #383938;
    font-weight: 600;
    font-size: 14px;
    font-family: 'Raleway', sans-serif;
    /* padding-top: 14px; */
}
.general-files-list .fileicon > div:first-child > span {
    font-family: Raleway;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #383938;
    margin-right: 10px;
}
.controlled-row{
    margin: 0 -30px;
}
.general-files-list  .fileicon {
    /* padding: 15px 0 0; */
}
.general-files-list  .fileicon svg path{
    /* fill: #98a6ad; */
}
