    color: #6c757d;
    padding: 13px 20px;
    border: none;
} */
.property-map-wrapper {
    padding-left: 0;
    padding-right: 0;
}
.property-status-hearing {
    font-size: 15px;
}

.editPropertyStatus {
    cursor: pointer;
    margin-left: 20px;
}

.editPropertyStatus svg {
    height: 15px;
    width: 15px;
}

.hearingStatusModal .modal-dialog {
    max-width: 800px;
}

.hearingStatusModal .modal-dialog svg path {
    fill: #FFBC00;
}

.hearingStatusModal .modal-dialog svg {
    height: 50px;
    width: 50px;
}
.hearingStatusModal h3{
    color: #6c757d;
    font-size: 20px;
}
.hearingStatusModal p {
    text-align: left;
    font-size: 15px;
    font-weight: 700;
    color: #6c757d;
}

.hearingStatusModal input {
    width: 100%;
    padding: 13px 10px;
    margin-top: 10px;
    background: #f1f3fa;
    border: none;
    font-size: 15px;
}
.hearingStatusModal .modal-content {
    border-radius: 4px;
    padding: 30px 25px 10px 25px;
}

.hearingstatus-btns {
    padding-top: 20px;
}
.hearingstatus-btns button:not(:last-child) {
    margin-right: 10px;
}

#add_billing_form h3 {
    font-size: 20px;
    font-weight: 700;
    line-height: 1;
}
.form__form-group-three{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.form__form-group-three > * {
    flex: 0 0 31%;
}